@import url('https://fonts.googleapis.com/css?family=Montserrat:400,700,900&display=swap');

body {
    margin: 0;
    width: 100%;
    height: 100vh;
    font-family: 'Montserrat', sans-serif;
    background-color: #343d4b;
    /* display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center; */
    background: #323b48;
    color: #fff;
}

:root {
    --gradient: linear-gradient(135deg, #72edf2 10%, #5151e5 100%);
}

* {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    line-height: 1.25em;
}

html {
    box-sizing: border-box;
}
*,
*:before,
*:after {
    box-sizing: inherit;
}

#main {
    max-width: 1000px;
    padding: 5em 2em;
    width: 100%;
    margin: 0 auto;
    color: #fff;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.container {
    border-radius: 25px;
    -webkit-box-shadow: 0 0 70px -10px rgba(0, 0, 0, 0.2);
    box-shadow: 0 0 70px -10px rgba(0, 0, 0, 0.2);
    background-color: #222831;
    color: #ffffff;
}

.weather-side {
    position: relative;
    height: 100%;
    border-radius: 25px;
    width: 300px;
    height: 400px;
    box-shadow: 0 0 20px -10px rgba(0, 0, 0, 0.2);
    transition: transform 300ms ease;
    transform: translateZ(0) scale(1.02) perspective(1000px);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}

.weather-side:hover {
    -webkit-transform: scale(1.1) perspective(1500px) rotateY(10deg);
    transform: scale(1.1) perspective(1500px) rotateY(10deg);
}

.weather-gradient {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    /* background-image: var(--gradient); */
    background: linear-gradient(45deg, black, rgba(0, 0, 0, 0.25));
    /* background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.75)); */
    border-radius: 25px;
    opacity: 0.8;
}

.date-container {
    position: absolute;
    top: 25px;
    left: 25px;
}

.date-dayname {
    margin: 0;
    margin-bottom: 10px;
}

.date-day {
    display: block;
}

.location {
    display: inline-block;
    margin-top: 10px;
}

.location-icon {
    display: inline-block;
    height: 0.8em;
    width: auto;
    margin-right: 5px;
}

.weather-container {
    position: absolute;
    bottom: 25px;
    left: 25px;
}

.weather-icon {
    height: 70px;
    width: auto;
}

.weather-temp {
    margin: 0;
    font-weight: 700;
    font-size: 4em;
}

.weather-desc {
    margin: 0;
}

/* .info-side {
    padding: 25px;
} */

.info-side {
    padding: 25px;
    margin-left: -10px;
    padding-left: 35px;
    background: #222831;
    width: 330px;
    border-top-right-radius: 25px;
    border-bottom-right-radius: 25px;
}

.today-info {
    padding: 15px;
    box-shadow: 0 0 50px -5px rgba(0, 0, 0, 0.25);
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    margin-bottom: 30px;
}

.today-info > div:not(:last-child) {
    margin: 0 0 10px 0;
}

.today-info > div .title {
    font-weight: 700;
}

.today-info > div {
    display: flex;
    justify-content: space-between;
}

.week-list {
    list-style-type: none;
    padding: 0;
    margin-bottom: 10px;
    -webkit-box-shadow: 0 0 50px -5px rgba(0, 0, 0, 0.25);
    box-shadow: 0 0 50px -5px rgba(0, 0, 0, 0.25);
    border-radius: 10px;
    display: flex;
}

.week-list > li {
    padding: 15px;
    cursor: pointer;
    -webkit-transition: 200ms ease;
    -o-transition: 200ms ease;
    transition: 200ms ease;
    border-radius: 10px;
}

.week-list > li:hover {
    -webkit-transform: scale(1.1);
    -ms-transform: scale(1.1);
    transform: scale(1.1);
    background: #fff;
    color: #222831;
    -webkit-box-shadow: 0 0 40px -5px rgba(0, 0, 0, 0.2);
    box-shadow: 0 0 40px -5px rgba(0, 0, 0, 0.2);
}

.week-list > li.active {
    background: #fff;
    color: #222831;
    border-radius: 10px;
}

.week-list > li .day-name {
    display: block;
    margin: 10px 0 0 0;
    text-align: center;
}

.week-list > li .day-icon {
    display: block;
    height: 30px;
    width: auto;
    margin: 0 auto;
}

.week-list > li .day-temp {
    display: block;
    text-align: center;
    margin: 10px 0 0 0;
    font-weight: 700;
}

.location-container {
    padding: 25px 35px;
}

.location-button {
    outline: none;
    width: 100%;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    border: none;
    border-radius: 25px;
    padding: 10px;
    font-family: 'Montserrat', sans-serif;
    background-image: var(--gradient);
    color: #ffffff;
    font-weight: 700;
    -webkit-box-shadow: 0 0 30px -5px rgba(0, 0, 0, 0.25);
    box-shadow: 0 0 30px -5px rgba(0, 0, 0, 0.25);
    cursor: pointer;
    -webkit-transition: -webkit-transform 200ms ease;
    transition: -webkit-transform 200ms ease;
    -o-transition: transform 200ms ease;
    transition: transform 200ms ease;
    transition: transform 200ms ease, -webkit-transform 200ms ease;
}

.location-button:hover {
    -webkit-transform: scale(0.95);
    -ms-transform: scale(0.95);
    transform: scale(0.95);
}

.location-button .feather {
    height: 1em;
    width: auto;
    margin-right: 5px;
}

.weather-content {
    display: flex;
    justify-content: center;
    box-shadow: 0 0 70px -10px rgba(0, 0, 0, 0.2);
    width: fit-content;
}

.search {
    margin: 0 auto 2em;
    max-width: 500px;
    width: 100%;
    text-align: center;
}

.search input {
    width: 100%;
    border-radius: 25px;
    padding: 10px 20px;
    border: none;
    font-size: 1em;
}

input[type='text']:focus {
    outline: none;
}

@media only screen and (max-width: 768px) {
    .container {
        box-shadow: 0 0 70px -10px rgba(0, 0, 0, 0.2);
        background-color: transparent !important;
        color: #ffffff;
    }

    .weather-content {
        flex-direction: column;
        align-items: center;
        box-shadow: none;
    }

    .info-side {
        margin-left: 0px !important;
        padding: 25px !important;
        margin-top: 2em;
        background: #222831;
        width: 330px;
        border-top-right-radius: 25px;
        border-bottom-right-radius: 25px;
        border-radius: 25px;
        box-shadow: 0 0 70px -10px rgba(0, 0, 0, 0.2);
    }

    .weather-side {
        box-shadow: 0 0 70px -10px rgba(0, 0, 0, 0.2);
    }
}
